import React from 'react';
import './Login.scss';
import authorizationImage from './images/authorizationImage.svg';
import FormsLogin from './FormsLogin/FormsLogin';
import { useLocation } from 'react-router-dom';


function Login () {
    const location = useLocation();
    //    location      
    const fromPage = location.state?.from?.pathname;

    return (
        <div className='container'>
            {fromPage && <div className='nonAuth'>     {fromPage},     .</div>}
            <div className='wrapper'>
                <div className='contentLeft'>
                    <h3 className='h h3'>    ,  .</h3>
                    <img src={authorizationImage} alt="" className='authorizationImage701'/>
                </div>
                <FormsLogin/>
                <img src={authorizationImage} alt=""
                className='authorizationImage700'/>
            </div>
        </div>
    )
}

export default Login;



