import React from 'react';
import "./Tariffs.scss";
import {useSelector} from 'react-redux';
import {useNavigate} from "react-router-dom";


//  TarrifsItem   2  /home (  Home->Tariffs)  /tariffs (  tariffsPage->Tariffs)
//     TariffsItem   /home    /tariffs         /tariffs.


function TarrifsItem ({tarif, getOffsetY}) {
    //        (    )
    const token = useSelector(state => state.loginReduer.token);
    const userData = useSelector(state => state.loginReduer.userData);

    // ,    TarrifsItem  
    const isCurrent = token && (tarif.title.toLowerCase() === userData.tarif.toLowerCase()) ? true : false;

    const navigate = useNavigate();

    //      /tariffs       
    //    /tariffs     
    const handleButton = () => {
        navigate("/tariffs", {state:{tarif: tarif.title}});
    }

    //           
    let ref = React.createRef();

    //       /tariffs    
    //      TariffsPage  
    React.useEffect(() => {
        if (getOffsetY) {
            let rect = ref.current.getBoundingClientRect();
            getOffsetY(tarif.title, rect.top);
        }
    }, []);


    return (
        <div className={`${isCurrent ? 'current' : ''} tarif`} style={{'--background': tarif.background}}>
            <div ref={ref}></div>
            <div 
                className='title' 
                style={{
                    '--background': tarif.background,
                    '--color': tarif.color,
                }}
            >
                <div className='title_text'>
                    <h5 className='h5 sec3_h5'>{tarif.title}</h5>
                    <p>{tarif.subTitle}</p>
                </div>
                <div className='img'>
                    <img src={tarif.img} alt=" " width="93"/>
                </div>
            </div>

            <div className='currentTariff'>
                {isCurrent && <div className='badge'> </div>}
            </div>

            <div className='text'>
                <p className='h5 sec3_h5'>
                    {tarif.price} &#8381; &emsp;
                    <span className='oldPrice'>{tarif.oldPrice} &#8381;</span>
                </p>

                <p className='installment'>{` ${tarif.priceInstallment} /.    24 .`}</p>

                <ul className='ul'> 
                    <p className='ul_title'>  :</p>
                    {tarif.bonus.map(item => {
                        return <li className='li' key={item}>{item}</li>
                    })}
                </ul>
                {getOffsetY && tarif.text}
            </div>

            {!getOffsetY && 
            <button 
                className={`buttonTariff ${isCurrent? 'buttonCurrent' : ''}`}
                onClick={handleButton}
            >
                {isCurrent? '   ' : ''}
            </button>}
        </div>
    )
}

export default TarrifsItem;